body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App{
  max-width: 400px;
 
  margin: 20px auto;
  text-align: center;
}


nav{
  padding: 10px;
  
}

nav ul{
  
    list-style: none;
  

}

nav ul li{
  display: inline-block;
  padding: 10px;
}

.book-list{
  padding: 20px;
  margin-bottom: 10px;
}

.book-list ul{
  list-style: none;
  padding: 0px;
  
}
.book-list ul li{
  padding: 10px;
  margin: 10px auto;
  border-radius: 5px;
  
}